import React, { useState, useEffect } from "react";
import Bweet from "components/Bweet";
import BweetForm from "components/BweetForm";
import { dbService } from "fbase";
import { collection, onSnapshot, orderBy, query } from "@firebase/firestore";

const Home = ({ userObj }) => {
  const [bweets, setBweets] = useState([]);

  useEffect(() => {
    const bweetsQueryByDesc = query(
      collection(dbService, "bweet"),
      orderBy("createdAt", "desc")
    );
    onSnapshot(bweetsQueryByDesc, (snapshot) => {
      const bweetArr = snapshot.docs.map((doc) => ({
        id: doc.id,
        ...doc.data(),
      }));
      setBweets(bweetArr);
    });
  }, []);

  return (
    <>
      <BweetForm userObj={userObj} />
      <div>
        {bweets.map((item) => (
          <Bweet
            key={item.id}
            bweetObj={item}
            isOwner={item.creatorId === userObj.uid}
          />
        ))}
      </div>
    </>
  );
};

export default Home;
