import { authService, dbService } from "fbase";
import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import {
  collection,
  orderBy,
  query,
  where,
  getDocs,
} from "@firebase/firestore";
import { updateProfile } from "@firebase/auth";

const Profile = ({ userObj, refreshUser }) => {
  const [myBweets, setMyBweets] = useState([]);
  const [newDisplayName, setNewDisplayName] = useState("");
  useEffect(() => {
    getMyBweets();
  }, []);

  const getMyBweets = async () => {
    const myBweetsQueryByDesc = query(
      collection(dbService, "bweet"),
      where("creatorId", "==", userObj.uid),
      orderBy("createdAt", "desc")
    );
    const myBweetsDocs = await getDocs(myBweetsQueryByDesc);
    const myBweetsArr = myBweetsDocs.docs.map((doc) => ({
      id: doc.id,
      ...doc.data(),
    }));
    setMyBweets(myBweetsArr);
  };

  const history = useHistory();
  const logOut = () => {
    authService.signOut();
    history.push("/");
  };

  const onChange = (event) => {
    const {
      target: { value },
    } = event;
    setNewDisplayName(value);
  };

  const onSubmit = async (event) => {
    event.preventDefault();
    await updateProfile(authService.currentUser, {
      displayName: newDisplayName,
    });
    refreshUser();
  };
  return (
    <>
      {userObj.displayName}'s Profile
      <button onClick={logOut}>Log Out</button>
      <div>
        {myBweets.map((item) => (
          <div key={item.id}>{item.text}</div>
        ))}
      </div>
      <form onSubmit={onSubmit}>
        <input
          value={newDisplayName}
          onChange={onChange}
          type="text"
          placeholder={userObj.displayName}
        ></input>
        <input type="submit" value="update"></input>
      </form>
    </>
  );
};

export default Profile;
