import React, { useState, useRef } from "react";
import { dbService, storageService } from "fbase";
import { addDoc, collection } from "@firebase/firestore";
import { ref, uploadString, getDownloadURL } from "@firebase/storage";
import { v4 as uuidv4 } from "uuid";

const BweetForm = ({ userObj }) => {
  const [bweet, setBweet] = useState("");
  const [image, setImage] = useState("");
  const imageInput = useRef();

  const onSubmit = async (event) => {
    event.preventDefault();
    let imageUrlFromStorage = "";
    if (image !== "") {
      const imagesRef = ref(storageService, `${userObj.uid}/${uuidv4()}`);
      await uploadString(imagesRef, image, "data_url");
      imageUrlFromStorage = await getDownloadURL(imagesRef);
    }
    const bweetWithImage = {
      text: bweet,
      createdAt: Date.now(),
      creatorId: userObj.uid,
      image: imageUrlFromStorage,
    };
    try {
      await addDoc(collection(dbService, "bweet"), bweetWithImage);
    } catch (error) {
      console.error(error);
    }
    setBweet("");
    setImage("");
  };

  const onChange = (event) => {
    const {
      target: { value },
    } = event;
    setBweet(value);
  };

  const previewImage = (event) => {
    const {
      target: { files },
    } = event;
    const targetImage = files[0];
    const reader = new FileReader();
    reader.onloadend = (event) => {
      const {
        currentTarget: { result },
      } = event;
      setImage(result);
    };
    reader.readAsDataURL(targetImage);
  };

  const clearImage = () => {
    setImage("");
    imageInput.current.value = "";
  };

  return (
    <form onSubmit={onSubmit}>
      <input
        value={bweet}
        onChange={onChange}
        type="text"
        placeholder="What's on your mind?"
        required
        maxLength={120}
      ></input>
      <input
        type="file"
        accept="image/*"
        onChange={previewImage}
        ref={imageInput}
      ></input>
      <input type="submit" value="Bweet"></input>
      {image && (
        <div>
          <img src={image} width="50px" height="50px" alt="Preview"></img>
          <button onClick={clearImage}>Clear</button>
        </div>
      )}
    </form>
  );
};

export default BweetForm;
