import React, { useState } from "react";
import { dbService, storageService } from "fbase";
import { deleteDoc, doc, updateDoc } from "@firebase/firestore";
import { ref, deleteObject } from "@firebase/storage";

const Bweet = ({ bweetObj, isOwner }) => {
  const [isEditing, setIsEditing] = useState(false);
  const [updatedBweet, setUpdatedBweet] = useState(bweetObj.text);

  const deleteBweet = async () => {
    const ok = window.confirm("Are you sure you wnat to delete this bweet?");
    if (ok) {
      await deleteDoc(doc(dbService, "bweet", bweetObj.id));
      await deleteObject(ref(storageService, bweetObj.image));
    }
  };

  const toggleIsEditing = () => {
    setIsEditing((prev) => !prev);
  };

  const onChange = (event) => {
    const {
      target: { value },
    } = event;
    setUpdatedBweet(value);
  };

  const onSubmit = async (event) => {
    event.preventDefault();
    const TargetDocRef = doc(dbService, "bweet", bweetObj.id);
    await updateDoc(TargetDocRef, {
      text: updatedBweet,
    });
    setIsEditing(false);
  };

  return (
    <div>
      {isEditing ? (
        <>
          <form onSubmit={onSubmit}>
            <input
              value={updatedBweet}
              onChange={onChange}
              type="text"
              placeholder="What's on your mind?"
              required
              maxLength={120}
            ></input>
            <input type="submit" value="Update a Bweet"></input>
          </form>
          <button onClick={toggleIsEditing}>Cancel</button>
        </>
      ) : (
        <>
          <h4>{bweetObj.text}</h4>
          {bweetObj.image && (
            <img
              src={bweetObj.image}
              width="50px"
              height="50px"
              alt="Bweet"
            ></img>
          )}
          {isOwner && (
            <>
              <button onClick={toggleIsEditing}>Update</button>
              <button onClick={deleteBweet}>Delete</button>
            </>
          )}
        </>
      )}
    </div>
  );
};

export default Bweet;
